    font-size: 15px;
  }
}

.container {
  box-sizing: border-box;
  width: 1200px;
  padding: 0 15px;
  margin: 0 auto;
}
.container:after {
  content: "";
  display: table;
  clear: both;
}
@media (max-width: 1199px) {
  .container {
    width: 768px;
    padding: 0 15px;
  }
}
@media (max-width: 767px) {
  .container {
    width: 320px;
    padding: 0 10px;
  }
}
.container--promo {
  padding: 160px 200px 240px 400px;
}
@media (max-width: 1199px) {
  .container--promo {
    padding: 140px 40px 240px 300px;
  }
}
@media (max-width: 767px) {
  .container--promo {
    padding: 40px 15px 100px 120px;
  }
}
.container--costs {
  padding: 0 220px 100px 220px;
  position: relative;
}
@media (max-width: 1199px) {
  .container--costs {
    padding: 0 15px;
    padding-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .container--costs {
    padding: 0 10px;
  }
}
.container--work {
  padding: 0 90px;
  position: relative;
}
@media (max-width: 1199px) {
  .container--work {
    padding: 0 15px;
  }
}
@media (max-width: 767px) {
  .container--work {
    padding: 0 10px;
  }
}
.container--map {
  position: relative;
  padding: 80px 100px;
}
@media (max-width: 1199px) {
  .container--map {
    padding: 60px 0px;
  }
}
@media (max-width: 767px) {
  .container--map {
    padding: 60px 10px;
    padding-top: 220px;
  }
}

h1, h2, p {
  margin: 0;
}

ul {
  padding: 0;
}

